.lightapp-component-cardTable-view {
  background-color: #fff;
  z-index: 20;
  overflow-y: auto;
}
.card-table-expand-wraps .no-data-placeholder {
  width: 80px;
  height: 26px;
  position: relative;
}
.card-table-expand-wraps .no-data-placeholder .no-data {
  background: url(../../../../../../static/images/nodata.png) center center / contain no-repeat;
  display: inline-block;
  height: 26px;
  width: 26px;
}
.card-table-expand-wraps .no-data-placeholder .no-data-title {
  position: absolute;
  width: 60px;
  top: 3px;
  padding-left: 8px;
  color: #aeaeae;
}
.card-table-expand-wraps .no-data-li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.card-table-expand-list {
  width: 100%;
  padding: 8px 76px 16px 40px;
  border-bottom: 1px solid #ccc;
}
.card-table-expand-list:last-child {
  border: none;
}
.card-table-expand-list .expanded-list-item {
  width: 50%;
  margin-top: 8px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 14px;
  display: inline-block;
}
.card-table-expand-list .expanded-list-item .expanded-list-item-label {
  width: 140px;
  max-width: 140px;
  min-width: 140px;
  line-height: 14px;
  padding-right: 20px;
  text-align: right;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #555;
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.card-table-expand-list .expanded-list-item .expanded-list-item-label .expanded-list-item-label-colon {
  position: absolute;
  left: 125px;
}
.card-table-expand-list .expanded-list-item .expanded-list-item-control {
  width: calc(100% - 140px);
  text-align: left;
  padding-right: 8px;
  position: relative;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #111;
  display: inline-block;
  vertical-align: middle;
}
.card-table-expand-list .expanded-list-item .expanded-list-item-control .expanded-list-item-control-wrapper {
  height: 28px;
  line-height: 14px;
  vertical-align: middle;
  white-space: normal;
  word-wrap: break-word;
  word-break: break-all;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  max-height: 40px;
  overflow: auto;
}
.card-table-expand-list .expanded-list-item .expanded-list-item-control .expanded-list-item-control-wrapper span {
  max-height: 28px;
}
