.cardTable-view-item {
  background-color: #fff;
}
.cardTable-view-item .expanded-row-item {
  width: 50%;
  margin-top: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 14px;
  display: inline-block;
}
.cardTable-view-item .expanded-row-item .expanded-row-item-label {
  width: 140px;
  max-width: 140px;
  min-width: 140px;
  line-height: 14px;
  word-wrap: break-word;
  white-space: normal;
  padding-right: 20px;
  text-align: right;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #555;
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.cardTable-view-item .expanded-row-item .expanded-row-item-label .expanded-row-item-label-colon {
  position: absolute;
  left: 125px;
}
.cardTable-view-item .expanded-row-item .expanded-row-item-control {
  width: calc(100% - 140px);
  text-align: left;
  padding-right: 8px;
  position: relative;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #111;
  display: inline-block;
  vertical-align: middle;
}
.cardTable-view-item .expanded-row-item .expanded-row-item-control .expanded-row-item-control-wrapper {
  height: 28px;
  line-height: 14px;
  vertical-align: middle;
  white-space: normal;
  word-wrap: break-word;
  word-break: break-all;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  max-height: 40px;
  overflow: auto;
}
.cardTable-view-item .expanded-row-item .expanded-row-item-control .expanded-row-item-control-wrapper span {
  max-height: 28px;
}
.cardTable-wrapper .u-table-content .u-table-tbody .u-table-row-expand-icon {
  display: none;
}
